.container {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;

    @media screen and (min-width: 768px) {
            max-width: 768px;
            padding-left: 32px;
            padding-right: 32px;
       
    }

    @media screen and (min-width: 1200px) {
        max-width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }
}
